.spinner-buy.burning .pending {
  -webkit-animation-name: none;
          animation-name: none;
  justify-content: flex-end;
}

.spinner-buy.burning .group {
  height: 620px;
  overflow: hidden;
}

.spinner-buy.burning .group-fire {
  width: 100%;
  -webkit-animation-name: example4;
          animation-name: example4;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  position: relative;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}

.spinner-buy.burning .group-fire figure {
  width: 100%;
  height: 320px;
  margin-bottom: 0;
}

.spinner-buy.burning .group-fire figure img {
  width: 100%;
  height: 100%;
  min-height: auto;
  object-fit: cover;
}

.spinner-buy.burning .group-fire figure.move-down {
  position: relative;
  top: 66px;
  z-index: 1;
}

.spinner-buy.burning .group-fire figure.move-up {
  position: relative;
  z-index: 2;
}

.spinner-buy .pending::before {
  content: '';
  top: -10px;
  left: 49.5%;
  transform: translateX(-50%);
  background: #000;
  height: 101.8%;
  width: 101.4%;
  z-index: -22;
  position: absolute;
}

.spinner-buy .pending::after {
  content: '';
  top: 0;
  left: 50.5%;
  transform: translateX(-50%);
  background: #000;
  height: 101.8%;
  width: 101.4%;
  z-index: -22;
  position: absolute;
}

.spinner-buy .pending .wrap {
  width: 332px;
  height: 444px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
}

.spinner-buy .pending .wrap img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.spinner-buy .pending .wrap img.buy-only {
  -webkit-animation-name: example2;
          animation-name: example2;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.spinner-buy .white-bar {
  width: 100%;
  height: 190px;
