.list__equip {
  background: url('../../../assets/img/background/bg-equip.png');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  padding: 60px 0;
}
.list__equip h2 {
  font-family: 'Minecraft', 'Dinkie';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  color: #020c16;
  text-align: center;
}
.list__equip--item .item {
  text-align: center;
}
.list__equip--item .item img {
  margin-bottom: 15px;
}
.list__equip .page-link {
  background-color: transparent;
  border: none;
}
.list__equip .pagination {
  justify-content: center;
  margin: 20px 0;
}
.list__equip .pagination a {
  font-family: 'Minecraft', 'Dinkie';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  color: #020C16;
  font-size: 20px;
}
.list__equip .page-item.active .page-link {
  background: #020C16;
}
@media (min-width: 768px) { 
  .list__equip .list__equip--item {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
  .list__equip .list__equip--item .item {
    width: fit-content;
    flex-direction: column;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }
}
@media (min-width: 1024px) { }
@media (min-width: 1200px) { }
